/* eslint-disable jsx-a11y/accessible-emoji */
import React, { useContext } from "react";
import styled from "styled-components";
import { AuthContext } from "../../firebase/Auth";
import CardButton from "../CardButton";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";

const rule = [
  <React.Fragment>
    1.     .
  </React.Fragment>,
  <React.Fragment>
    2.  <span> </span>    
    .
  </React.Fragment>,
  <React.Fragment>
    3.   <span> </span>   
    .
  </React.Fragment>,
  <React.Fragment>
    4.    ,   
    <span>  </span> .
  </React.Fragment>,
  <React.Fragment>
    5.         .
    <p>     .</p>
  </React.Fragment>,
  <React.Fragment>
    6.     ,   
    <span> </span>,    
    <span> </span>.
  </React.Fragment>,
];

function More() {
  const { version = "", fetchRemoteConfig } = useContext(AuthContext);
  const ruleList = rule.map((element: any, index: number) => (
    <StyledContext key={index}>{element}</StyledContext>
  ));
  return (
    <StyledMoreWrapper>
      <StyledAccordion>
        <AccordionSummary expandIcon={<ExpandMoreIcon />}>
           
        </AccordionSummary>
        <StyledAccordionDetails>{ruleList}</StyledAccordionDetails>
      </StyledAccordion>
      <CardButton cardSize="primary" onClick={fetchRemoteConfig}>
        <StyledSync>
          <span>  DB </span>
          <span style={{ color: "#777" }}>v{version}</span>
        </StyledSync>
      </CardButton>
      {/* <CardButton cardSize="primary">     </CardButton> */}
    </StyledMoreWrapper>
  );
}

export default More;

const StyledMoreWrapper = styled.div`
  & > * + * {
    margin-top: 20px;
  }
`;

const StyledContext = styled.div`
  color: black;
  font-size: 14px;
  line-height: 1.5;
  & > span {
    color: #bd4358;
  }
  & > p {
    margin-top: 5px;
  }
`;

const StyledAccordion = styled(Accordion)`
  font-size: 18px;
  font-weight: bold;
  border-radius: 20px !important;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
`;

const StyledAccordionDetails = styled(AccordionDetails)`
  & > * + * {
    margin-top: 15px;
  }
`;

const StyledSync = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
`;
